import { reactive, toRefs } from 'vue'
import { useStore } from 'vuex'

export function useBarConfig () {
  const store = useStore()
  const { dispatch } = store

  const states = reactive({
    data: {
      labels: [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December'
      ],
      datasets: [
        {
          label: '',
          backgroundColor: '#7CA3D5',
          // backgroundColor: [  // 
          //   'rgba(255, 99, 132, 0.2)',
          //   'rgba(54, 162, 235, 0.2)',
          //   'rgba(255, 206, 86, 0.2)',
          //   'rgba(75, 192, 192, 0.2)',
          //   'rgba(153, 102, 255, 0.2)',
          //   'rgba(255, 159, 64, 0.2)'
          // ],
          hoverBackgroundColor: '#5F8AC1',
          data: getRandomData(), // [82, 94, 65, 53, 5, 10, 76, 77, 34, 20, 93, 93, 8]
          //  
          datalabels: {
            labels: {
              title: {
                color: 'blue',
                font: { size: 20 }
                // 
                // anchor: 'end',
                // align: 'top'
              },
              value: {
                color: '#00B4FF',
                font: { size: 21 }
                // 
                // anchor: 'end',
                // align: 'top'
              }
            },
            // 
            listeners: {
              click: function (context, e) {
                if (!context.active) return
                console.log('context', context)
                console.log('e', e)

                const index = context.dataIndex
                const { label } = context.dataset
                const month = states.data.labels[index]
                const value = context.dataset.data[index]
                const content = `
                 ${label}  ${index + 1}  
                ( ${month} )   ${value} 
                `

                dispatch('chart/getChartContent', content)
              }
            }
          }
        },
        {
          label: '',
          backgroundColor: '#DA9B9B',
          hoverBackgroundColor: '#CF8B8B',
          data: getRandomData(),
          //  
          datalabels: {
            labels: {
              title: {
                color: 'red',
                font: { size: 20 }
              },
              value: {
                color: 'pink',
                font: { size: 21 }
              }
            },
            listeners: {
              click: function (context, e) {
                if (!context.active) return
                console.log('context', context)
                console.log('e', e)

                const index = context.dataIndex
                const { label } = context.dataset
                const month = states.data.labels[index]
                const value = context.dataset.data[index]
                const content = `
                 ${label}  ${index + 1}  
                ( ${month} )   ${value} 
                `

                dispatch('chart/getChartContent', content)
              }
            }
          }
        }
      ]
    },
    options: {
      // indexAxis: 'y', // 
      responsive: true,
      maintainAspectRatio: false,
      plugins: {
        //  
        datalabels: {
          labels: {
            value: {
              color: '#fff'
            },
            title: {
              color: '#fff'
            }
          }
        },
        // 
        legend: {
          labels: {
            font: {
              size: 20
            },
            color: 'gray'
          }
        }
      }
    }
  })

  function getRandomData () {
    const data = []
    for (let i = 0; i <= 12; i++) {
      data.push(Math.round(Math.random() * 100) + 1)
    }

    return data
  }

  return {
    ...toRefs(states)
  }
}
